.tittle {
  margin-bottom: 14px;

  font-feature-settings: 'liga' off;
  color: var(--secondary-text-color);
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 114.28%;
  letter-spacing: -0.56px;
}

.text {
  margin-bottom: 30px;

  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 128.5%;
  letter-spacing: -0.28px;
}

